.image-stack {
  display: grid;
  position: relative;
  /* grid-template-columns: repeat(12, 1fr); */
  grid-template-columns: repeat(8, 1fr);
  /* grid-template-columns: repeat(8, 12%); */
  /* grid-template-rows: repeat(6, 10%); */
  grid-template-rows: repeat(33, 3%);
  rotate: z 1deg;
  /* width: 700px; */
  /* max-height: 358px; */
}

.image-stack__item--bottom {
  grid-column: 1 / span 8;
  grid-row: 4;
  z-index: 1;
  /* position: relative; */
}

.image-stack__item--top2 {
  grid-row: 4;
  grid-column: 1 / span 8;
  z-index: 4;

  /* position: relative; */
}


.image-stack__item--top {
  grid-row: 4;
  grid-column: 1 / span 8;
  z-index: 3;

  /* position: relative; */
}

.image-stack__item--middle {
  grid-row: 9 / span 16;
  grid-column: 2 / span 6;
  z-index: 2;
  margin-left: 2%;
  margin-right: 8%;
  /* position: relative; */
}

.image-stack__item--top-trick-title {
  grid-row: 25 / span 3;
  grid-column: 4;
  z-index: 5;
  /* margin-top: 10.5%; */
  margin: 0;
  background-color: #EBEBDB;
  rotate: z -3deg;
}

img {
  width: 100%;
  display: block;
  position: relative;
}

